
<template>

    <div>
        <el-tag type="warning"  style="margin-left: 20px;margin-bottom: 20px;width: 200px;height: 40px;text-align: center;line-height: 40px;font-size: 20px">轮渡验票列表</el-tag>
        <el-col :span="23" style=" padding-bottom: 30px;">
            <el-row>
                <el-col class="el-col"  style="margin-left: 0;width: 500px">
                    <p class="input-title">验票时间:</p>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.startValidateTime"
                        type="datetime"
                        placeholder="开始时间"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        @change = "getTimeValue1">
                    </el-date-picker>
                    <span style="float: left;line-height: 40px">&nbsp;&nbsp;至&nbsp;&nbsp;</span>
                    <el-date-picker
                        style="float: left"
                        v-model="searchForm.endValidateTime"
                        type="datetime"
                        placeholder="结束时间"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        @change = "getTimeValue2"
                    >
                    </el-date-picker>
                </el-col>
                <el-col :span="5" class="el-col"  style="margin-left: 0">
                    <p class="input-title">发车日期:</p>
                    <el-date-picker
                        v-model="searchForm.depTime"
                        type="date"
                        value-format="yyyy-MM-dd HH:mm:ss"
                        placeholder="选择日期">
                    </el-date-picker>

                    <!--<el-date-picker-->
                        <!--style="float: left"-->
                        <!--v-model="searchForm.depTime"-->
                        <!--type="datetime"-->
                        <!--placeholder="结束时间"-->
                        <!--value-format="yyyy-MM-dd"-->
                        <!--@change = "getTimeValue2"-->
                    <!--&gt;-->
                    <!--</el-date-picker>-->

                </el-col>
                <el-col :span="3" class="el-col"  style="margin-left: 20px">
                    <p class="input-title">班次:</p>
                    <el-select  v-model="searchForm.scheduleId"  placeholder="请输入班次" >
                        <el-option
                            v-for="item in scheduleOption"
                            :key="item.id"
                            :label="item.shiftNum"
                            :value="item.id">
                        </el-option>
                    </el-select>

                </el-col>
                <el-col :span="1.5" class="el-col" style="">
                    <p class="input-title">&nbsp;</p>
                    <el-button type="primary" icon="el-icon-search" @click = "search">查询</el-button>
                    <el-button type="primary" icon="el-icon-search" @click = "clear">清空</el-button>
                </el-col>
            </el-row>
        </el-col>


        <el-col :span="23" style=" padding-top: 30px;">
            <table class="content-table">
                <tr>
                    <th>验票日期</th>
                    <th>乘船人</th>
                    <th>证件类型</th>
                    <th>证件号码</th>
                    <th>班次号</th>
                    <th>乘船吗</th>
                </tr>
                <tr v-for="(item,index) in aaData.data">
                    <td>{{item.validateTime}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.certiTypeText}}</td>
                    <td>{{item.certiNum}}</td>
                    <td>{{item.shiftNum}}</td>
                    <td>{{item.ticketNum}}</td>
                </tr>
                <tr>
                  <td colspan="6">（请根据发车日期查询）
                      总乘客数 {{aaData.totalPassengerCount}}人,&nbsp;&nbsp;
                      已核验乘客数 {{aaData.validatePassengerCount}}人,&nbsp;&nbsp;
                      未核验乘客数 {{aaData.waitValidateCount}}人
                  </td>
                </tr>
            </table>
            <div class="block page-f" style="position: relative;width: 100%">

                <el-pagination
                    style="position: absolute;right: 0"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage"
                    :page-size="pageSize"
                    :page-sizes="[10, 20, 30, 40]"
                    layout="total,prev, pager, next, jumper"
                    :total="aaData.totalCount">
                </el-pagination>
            </div>
            <div style="height: 80px"></div>
        </el-col>


    </div>
</template>
<script>
    import ajax from "../model/sys/index"
    const cityOptions = ['1', '2', '3', '4', '5', '6', '5', '6',];

    export default {
        data() {
            return {
                value:'',
                options:cityOptions,
                currentPage: 1,//默认页码
                pageSize:10,//每页显示条数
                totalNum:10,//总条数
                aaData:[],
                addvisible:false,
                from:"",
                to:"",
                searchForm: {
                    startValidateTime:"",
                    endValidateTime:"",
                    scheduleId:"",
                    depTime:""
                },
                formLabelWidth: '120px',
                scanurl:"",
                serialId:"",
                disabled:"",
                placeholder:"",
                token:"",
                scheduleOption:""
            };
        },
        methods: {
            clear(){
                this.searchForm.startValidateTime=""
                this.searchForm.endValidateTime=""
                this.searchForm.scheduleId=""
                this.searchForm.depTime=""
                this.ticktNum = ""
            },
            getOrderList:function () {
                var data  = this.searchForm
                data.pageIndex=this.currentPage
                data.pageSize=this.pageSize
//                if(data.depTime){
//                    data.depTime = data.depTime+" "+"00:00:00"
//                }
                for(var i in data){
                    if(String(data[i])==""){
                        delete data[i]
                    }
                }
                ajax.getValidateList(data,res=>{
                    if(res){
                        this.aaData = res.data.result
                    }


                })
            },
            GetQueryString(param,url) { //param为要获取的参数名 注:获取不到是为null
                var currentUrl = url; //获取当前链接
                var arr = currentUrl.split("?");//分割域名和参数界限
                if (arr.length > 1) {
                    arr = arr[1].split("&");//分割参数
                    for (var i = 0; i < arr.length; i++) {
                        var tem = arr[i].split("="); //分割参数名和参数内容
                        if (tem[0] == param) {
                            return tem[1];
                        }
                    }
                    return null;
                }
                else {
                    return null;
                }
            },

            ticketInput(){

                if(this.scanurl.indexOf("code")!=-1){

                    var thisUrl = this.scanurl
                    this.scanurl = this.GetQueryString('ticketNum',thisUrl)
                    this.ticktNum = this.GetQueryString('ticketNum',thisUrl)
                    this.serialId = this.GetQueryString('serialId',thisUrl)
                    this.token = localStorage.getItem("token")
                }else {

                    this.ticktNum = this.scanurl
                    this.token = localStorage.getItem("token")
                    this.serialId=""
                }
            },

            checkUp(){
                var params = {
                    st:this.token,
                    serialId: this.serialId,
                    ticktNum:this.ticktNum,
                    token:this.token
                }
                for(var i in params){
                    if(String(params[i])==""){
                        delete params[i]
                    }
                }
                ajax.orderValidate(params,res=>{
                    if(res){
                        this.$alert('核销成功', '您好！', {
                            confirmButtonText: '确定',
                            callback: action => {
                                this.scanurl=""
                            }
                        });
                    }else {
                        this.$alert('核销失败', '请注意！', {
                            confirmButtonText: '确定',
                            callback: action => {
                                this.scanurl=""
                            }
                        });
                    }
                })

                //orderValidate

            },
            search:function () {
                this.currentPage = 1
                this.getOrderList ()

            },
            orderExport:function () {
                var orderData  = this.searchForm
                for(var i in orderData){
                    if(String(orderData[i])==""){
                        delete orderData[i]
                    }
                }
                ajax.orderExport(orderData)
            },
            addDevice:function () {
                this.dialogFormVisible = true
            },
            getTimeValue1:function (val) {

                if(val==null){
                    this.searchForm.startValidateTime = ""
                }else {
                    this.searchForm.startValidateTime = val
                }
            },
            getTimeValue2:function (val) {

                if(val==null){
                    this.searchForm.endValidateTime = ""
                }else {
                    this.searchForm.endValidateTime = val
                }
            },
            shipSchsearchAll(){
                ajax.shipSchsearchAll({},res=>{
                    if(res){
                        this.scheduleOption = res.data.result
                    }
                })
            },

            handleSizeChange(val) {
                this.pageSize = val
                this.getOrderList ()
            },
            handleCurrentChange(val) {
                this.currentPage = val
                this.getOrderList ()
            }
        },
        mounted(){
            this.shipSchsearchAll()
            this.getOrderList()
         }
    }
</script>
<style>
    .input-title{
        font-size: 14px !important;
        font-weight: 600;
    }
    .el-col{
        margin-left: 20px;
    }
    ::-webkit-scrollbar-thumb{
        background-color: gainsboro;
    }
    .cell{
        text-align: center;
    }
    #content-main{
        overflow: scroll;
    }
    .el-table{
        overflow: scroll;
    }
    .content-table{
        width: 100%;
        border-collapse:collapse;
        background-color: white;
        text-align: center;
    }
    .content-table th{
        height: 40px;
        border: 1px solid #F2F3F4;
        text-align: center;
        background-color: gainsboro;
    }
    .content-table td{
        height: 40px;
        border: 1px solid #F2F3F4;
    }
    .dothis{
        color: #3C9CFC;
        cursor:default;
        margin-left: 10px;
    }
    .page-f{
        margin-top: 20px;
        float: right;
    }
    .content-table span:hover{
        color: silver;
    }
    .el-col-23{
        width: 99% !important;
    }
</style>



